import React, { Component } from 'react';
import { Link } from 'react-router-dom';
import { Table, Button, Popconfirm, notification, Icon } from 'antd';
import { http } from '../../services';
const { Column } = Table;

const columns = [
	{
		title: 'Nama Layanan',
		dataIndex: 'name',
		key: 'name'
	},
	{
		title: 'Harga',
		dataIndex: 'price',
		key: 'price'
	},
	{
		title: 'Waktu Pengerjaan',
		dataIndex: 'day_service',
		key: 'day_service',
		render: text => text + ' hari'
	},
	{
		title: 'Minimal',
		dataIndex: 'minimum',
		key: 'minimum'
	},
	{
		title: 'Satuan',
		dataIndex: 'unit',
		key: 'unit'
	},
	{
		title: 'Tipe',
		dataIndex: 'type',
		key: 'type'
	},
];

export default class ServiceList extends Component {

	constructor() {
		super();
		this.state = {
			columns: [],
			service_list: [],
			loading: false
		};
	}

	componentDidMount() {
		this.setState({ columns });
		this.getService();
	}

	getService() {
		this.setState({ loading: true});
		http.get('service').then(data => {
			this.setState({
				service_list: data,
				loading: false
			});
		});
	}

	delete(id) {
		http.delete('service/' + id).then(data => {
			notification.success({
				message: 'Sukses',
				description: data.message
			});
			this.getService();
		});
	}

	renderColumn() {
		return this.state.columns.map(column =>
			<Column title={column.title} dataIndex={column.dataIndex} key={column.key}></Column>
		);
	}

	render() {
		if (this.state.loading) {
			return (
				<div className="text-center">
					<Icon type="loading" style={{ fontSize: 64 }} />
				</div>
			);
		}
		return (
			<Table dataSource={this.state.service_list}>
				{this.renderColumn()}
				<Column className="text-center" title="Aksi" key="action" render={(text, record) => (
					<div>
						<Link className="ant-btn ant-btn-primary m-r-5" to={'/services/edit/' + record.id}>Ubah</Link>
						<Popconfirm
							title={'Hapus ' + record.name + ' ?'}
							onConfirm={() => this.delete(record.id)}
							okText="OK" cancelText="Batal">
							<Button type="danger">Hapus</Button>
						</Popconfirm>
					</div>
				)}/>
			</Table>
		);
	}

}



